* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
}

.home,
.projects,
.skills,
.contact {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    flex-direction: column;
    text-align: center;
    padding: 0 20px;
}

.projects {
    background-image: url(../public/images/totoro_smile.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #242424;
    font-size: 40px;
}

.skills {
    background-image: url(../public/images/totoro_wait.jpg);
    background-position: bottom;
    background-size: cover;
    background-repeat: no-repeat;
    color: #242424;
    font-size: 40px;
}

.contact {
    background-image: url(../public/images/blue_background.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #242424;
    font-size: 40px;
    display: flex;
    flex-direction: column;
}

.contact-header {
    color: #fff;
    font-size: 20px;
    width: fit-content;
}

.contact-wrapper {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    margin-left: 2px;
    width: 100%;
}

.contact-icon-group {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    margin-right: 10px;
    flex-direction: column;
}

.contact-info-group {
    display: flex;
    align-items: 10px;
    margin-bottom: 10px;
    margin-right: 10px;
    flex-direction: column;
}

.contact-icon {
    align-items: center;
    margin-bottom: 10px;
    color: #fff;
    font-size: 20px;
}

.contact-info {
    color: #fff;
    font-size: 20px;
}
